import React, { Component } from "react";
import { ResponsiveLine } from "@nivo/line";
import { SatisfactionDataset } from "../types/admin";

/**
 * @fileoverview This file contains the AbstractStatisticsGraph component.
 * @created Feb 19th, 2024
 * @lastModified Feb 19th, 2024
 *
 * @todo    AbstractStatisticsFile was created to abstract the line chart component
 *          from UserSatisfactionGraph and ChatBotResponseGraph. But we decided to
 *          use a scatterplot, so this level of abstraction is unnecessary.
 *          Future abstraction should just be for intervalTypes.
 */

/**
 * @interface AbstractStatisticsGraphProps
 * @description The props for the AbstractStatisticsGraph component
 * @property {satisfactionDataset} data - The data for the chart, consisting of an array of series objects.
 * @property {string} title - The title for the chart.
 * @property {(point: any) => TSX.Element} tooltipContent - The content for the tooltip.
 * @property {string} yAxisTitle - The title for the y-axis.
 * @property {string} intervalType - The type of interval for the x-axis.
 * @returns {TSX.Element} A JSX element to render a line chart for average user satisfaction per interval.
 * @requires @nivo/line
 */
interface AbstractStatisticsGraphProps {
  data: SatisfactionDataset;
  title: string;
  tooltipContent: (point: any) => JSX.Element;
  yAxisTitle: string;
  intervalType: string;
}

/**
 * A functional component for SummaryPage.tsx
 * @param AbstractStatisticsGraphProps - The props for the component
 * @returns a TSX element to render a line chart for average user satisfaction per interval.
 */
const AbstractStatisticsGraph: React.FC<AbstractStatisticsGraphProps> = ({
  data,
  title,
  tooltipContent,
  yAxisTitle,
  intervalType,
}) => {
  let tickVal = "";
  let tickFormat = "";

  // Determine the interval adjustment based on the intervalType
  switch (intervalType) {
    case "hours":
      tickVal = "every 1 hour";
      tickFormat = "%H:%M";
      break;
    case "days":
      tickVal = "every 1 day";
      tickFormat = "%m-%d";
      break;
    case "weeks":
      tickVal = "every 1 week";
      tickFormat = "%m-%d";
      break;
    case "months":
      tickVal = "every 1 month";
      tickFormat = "%Y-%m";
      break;
    default:
      throw new Error("Invalid interval type");
  }

  return (
    <>
      <div style={{ height: "400px", width: "45%", alignContent: "" }}>
        <h2 style={{ textAlign: "center" }}>{title}</h2>
        <ResponsiveLine
          data={data}
          colors={["#0067C6"]}
          tooltip={tooltipContent}
          margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
          xScale={{
            type: "time",
            format: "%Y-%m-%d %H:%M:%S",
          }}
          xFormat="time:%Y-%m-%d %H:%M"
          yScale={{
            type: "linear",
            min: "auto",
            max: "auto",
            stacked: true,
            reverse: false,
          }}
          yFormat=" >-.2f"
          curve="linear"
          axisTop={null}
          axisRight={null}
          axisBottom={{
            format: tickFormat,
            tickValues: tickVal,
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: "Time",
            legendOffset: 36,
            legendPosition: "middle",
          }}
          axisLeft={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: yAxisTitle,
            legendOffset: -40,
            legendPosition: "middle",
          }}
          pointSize={5}
          pointColor={"#0067C6"}
          pointBorderWidth={2}
          pointBorderColor={"#0067C6"}
          pointLabelYOffset={-12}
          useMesh={true}
        />
      </div>
    </>
  );
};

export default AbstractStatisticsGraph;
