import React, { Component } from "react";
import { ResponsiveLine } from "@nivo/line";
import AbstractStatisticsGraph from "./AbstractStatisticsGraph";
import { SatisfactionDataset, IntervalType } from "../types/admin";

/**
 * @interface UserSatisfactionGraphProps
 * @description The props for the UserSatisfactionGraph component
 * @property {SatisfactionDataset} data - The data for the chart, consisting of an array of series objects.
 * @property {string} intervalType - The type of interval for the x-axis.
 */
interface UserSatisfactionGraphProps {
  data: SatisfactionDataset;
  intervalType: IntervalType;
}

/**
 * A functional component for SummaryPage.tsx
 * @param UserSatisfactionGraphProps - The props for the component
 * @returns a TSX element to render a line graph for user satisfaction.
 *          by cropping the data to the last 12 hours/days/weeks/months
 *          specified by the intervalType.
 */
const UserSatisfactionGraph: React.FC<UserSatisfactionGraphProps> = ({
  data,
  intervalType,
}) => {
  const transformedData = transformData(data, intervalType);

  // this is not type safe
  const tooltip = ({ point }: { point: any }) => {
    console.log(point);
    return (
      <div
        style={{
          background: "white",
          padding: "9px 12px",
          border: "1px solid #ccc",
        }}
      >
        <div>
          <b style={{ color: "#0067C6" }}>
            Satisfaction on {point.data.xFormatted}
          </b>
        </div>
        <hr style={{ border: "1px solid #0067C6" }} />
        <div>
          <b style={{ color: "#0067C6" }}>Avg Rating:</b>{" "}
          {point.data.yFormatted}
        </div>
        <div>
          <b style={{ color: "#0067C6" }}>Based on </b> {point.data.count}{" "}
          ratings
        </div>
      </div>
    );
  };

  return (
    <AbstractStatisticsGraph
      data={transformedData}
      title="Average User Satisfaction"
      tooltipContent={tooltip}
      yAxisTitle="User Rating [0,10]"
      intervalType={intervalType}
    />
  );
};

function transformData(
  data: SatisfactionDataset,
  intervalType: IntervalType
): SatisfactionDataset {
  console.log("hello universe: ", data[0].data);
  const dataPoints = data[0].data;
  const maxDate = new Date(Math.max(...dataPoints.map((d) => d.x.getTime())));
  const startDate = new Date(maxDate);

  // Determine the interval adjustment based on the intervalType
  switch (intervalType) {
    case "hours":
      startDate.setHours(startDate.getHours() - 11);
      break;
    case "days":
      startDate.setDate(startDate.getDate() - 11);
      break;
    case "weeks":
      startDate.setDate(startDate.getDate() - 11 * 7); // 11 weeks back
      break;
    case "months":
      startDate.setMonth(startDate.getMonth() - 11); // 11 months back
      break;
    default:
      throw new Error("Invalid interval type");
  }

  const intervals: any[] = [];
  for (let i = 0; i < 12; i++) {
    console.log("pusing ", i);
    const date = new Date(startDate);
    switch (intervalType) {
      case "hours":
        date.setHours(startDate.getHours() + i);
        break;
      case "days":
        date.setDate(startDate.getDate() + i);
        break;
      case "weeks":
        date.setDate(startDate.getDate() + i * 7);
        break;
      case "months":
        date.setMonth(startDate.getMonth() + i);
        break;
    }
    intervals.push({ x: date, y: 0, count: 0 });
  }

  // TODO: maybe make this into another function !!!
  dataPoints.forEach((point) => {
    let index;
    switch (intervalType) {
      case "hours":
        index = Math.floor(
          (point.x.getTime() - startDate.getTime()) / (1000 * 60 * 60)
        );
        break;
      case "days":
        index = Math.floor(
          (point.x.getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24)
        );
        break;
      case "weeks":
        index = Math.floor(
          (point.x.getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24 * 7)
        );
        break;
      case "months":
        const yearDiff = point.x.getFullYear() - startDate.getFullYear();
        const monthDiff = point.x.getMonth() - startDate.getMonth();
        index = yearDiff * 12 + monthDiff;
        break;
    }

    if (index && index >= 0 && index < 12) {
      intervals[index].y += point.y;
      intervals[index].count += 1;
    }
  });

  const result = intervals.map((interval) => {
    if (interval.count > 0) {
      interval.y /= interval.count;
    }
    interval.x = new Date(
      interval.x.getFullYear(),
      interval.x.getMonth(),
      interval.x.getDate(),
      interval.x.getHours(),
      0,
      0,
      0
    );
    return interval;
  });

  return [{ id: "transformed data", data: result }];
}

export default UserSatisfactionGraph;
