const UserRatings = () => {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState({});

  const override: CSSProperties = {
    display: "block",
    margin: "0 auto",
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        await mockRequest(true, 2000);
        setData({ nodes: nodes });
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, []);

  const theme = useTheme([
    getTheme(),
    {
      Table: `
        --data-table-library_grid-template-columns:  50px auto auto 150px 150px 150px 150px;
        margin-bottom: 50px;
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px;
      `,
    },
  ]);

  const COLUMNS = [
    { label: "#", renderCell: (item: any) => item.id },
    { label: "Question", renderCell: (item: any) => item.question },
    { label: "Answer", renderCell: (item: any) => item.answer },
    { label: "Comment", renderCell: (item: any) => item.comment },
    {
      label: "Follow up Questions",
      renderCell: (item: any) => item.followUpQuestions,
    },
    { label: "Satisfaction", renderCell: (item: any) => item.satisfaction },
    {
      label: "Date",
      renderCell: (item: any) =>
        item.date.toLocaleDateString("en-US", {
          month: "short",
          day: "numeric",
          year: "numeric",
        }),
    },
  ];

  return (
    <>
      <Nav />
      <div className="ratings-page">
        <div className="clip-loader">
          <ClipLoader
            color={"#0050F8"}
            loading={loading}
            cssOverride={override}
            size={80}
            aria-label="Loading Spinner"
            data-testid="loader"
          />
        </div>
        {!loading && (
          <div className="user-ratings-container">
            <h2 className="user-ratings-title">User Ratings</h2>
            <p className="last-updated">Last Updated: 02/17/2024 12:01 PST</p>
            <CompactTable
              columns={COLUMNS}
              data={data}
              theme={theme}
              layout={{ custom: true }}
            />
          </div>
        )}
      </div>
    </>
  );
};

export default UserRatings;
