import { Link } from "react-router-dom";
import "../styles/Nav.css";

const Nav = () => {
  return (
    <nav className="navbar">
      <Link to="/summary" className="coast-companion">
        coast<span style={{ fontWeight: "200" }}>companion</span>
      </Link>
      <ul>
        <li>
          <div className="dropdown">
            <button className="dropdown-button">
              Metrics <span style={{ fontSize: "9px" }}>&#9660;</span>
            </button>
            <div className="dropdown-content">
              <Link to="/summary">Summary</Link>
              <Link to="/user-ratings">User Ratings</Link>
            </div>
          </div>
        </li>
        <li>
          <div className="dropdown">
            <Link className="dropdown-button" to="/maintain-kb">
              Knowledge Base
            </Link>
          </div>
        </li>
        <li className="logout">
          <Link to="/login">LOG OUT</Link>
        </li>
      </ul>
    </nav>
  );
};

export default Nav;
