import React, { Component } from "react";
import { ResponsiveScatterPlot } from "@nivo/scatterplot";
import { IntervalType, ChatBotResponseDataset } from "../types/admin";

/**
 * @fileoverview This file contains the ChatBotResponseGraph component.
 * @created Feb 19th, 2024
 * @lastModified Feb 19th, 2024
 *
 * @todo    The tooltip for the scatterplot is not type safe (lol!)
 */

/**
 * @interface ChatBotResponseGraphProps
 * @description The props for the ChatBotResponseGraph component
 * @property {chatBotResponseDataset} data - The data for the chart, consisting of an array of series objects.
 * @property {string} intervalType - The type of interval for the x-axis.
 */
interface ChatBotResponseGraphProps {
  data: ChatBotResponseDataset;
  intervalType: IntervalType;
}

/**
 * A functional component for SummaryPage.tsx
 * @param ChatBotResponseGraphProps - The props for the component
 * @returns a TSX element to render a scatterplot for chatbot response time.
 *          by cropping the data to the last 12 hours/days/weeks/months
 *          specified by the intervalType.
 */
const ChatBotResponseGraph: React.FC<ChatBotResponseGraphProps> = ({
  data,
  intervalType,
}) => {
  const tooltip = (point: any) => {
    const { chat_id, x, y } = point.node.data;
    let xFormatted = x.toString();
    console.log(point.node.data);

    return (
      <div
        style={{
          background: "white",
          padding: "9px 12px",
          border: "1px solid #ccc",
        }}
      >
        {
          <div>
            <b style={{ color: "#0067C6" }}>Chat {chat_id}</b>
          </div>
        }
        <hr style={{ border: "1px solid #0067C6" }} />
        {
          <div>
            <b style={{ color: "#0067C6" }}>Time:</b> {xFormatted}
          </div>
        }
        {
          <div>
            <b style={{ color: "#0067C6" }}>Avg:</b> {y} ms
          </div>
        }
      </div>
    );
  };
  let croppedData: ChatBotResponseDataset = cropData(data, intervalType);
  return (
    <AbstractChatBotResponseGraph
      data={croppedData}
      title="Chatbot Response Time"
      tooltipContent={tooltip}
      yAxisTitle="Response Time (ms)"
      intervalType={intervalType}
    />
  );
};

/**
 * @interface AbstractChatBotResponseGraphProps
 * @description The props for the AbstractChatBotResponseGraph component
 * @property {chatBotResponseDataset} data - The data for the chart, consisting of an array of series objects.
 * @property {string} title - The title for the chart.
 * @property {(point: any) => TSX.Element} tooltipContent - The content for the tooltip.
 * @property {string} yAxisTitle - The title for the y-axis.
 * @property {string} intervalType - The type of interval for the x-axis.
 */
interface AbstractChatBotResponseGraphProps {
  data: ChatBotResponseDataset;
  title: string;
  tooltipContent: (point: any) => JSX.Element;
  yAxisTitle: string;
  intervalType: IntervalType;
}

/**
 * A functional component for SummaryPage.tsx
 * @param AbstractChatBotResponseGraphProps - The props for the component
 * @returns a TSX element to render a scatterplot for chatbot response time.
 */
const AbstractChatBotResponseGraph: React.FC<
  AbstractChatBotResponseGraphProps
> = ({ data, title, tooltipContent, yAxisTitle, intervalType }) => {
  let tickVal = "";
  let tickFormat = "";

  switch (intervalType) {
    case "hours":
      tickVal = "every 1 hour";
      tickFormat = "%H:%M";
      break;
    case "days":
      tickVal = "every 1 day";
      tickFormat = "%m-%d";
      break;
    case "weeks":
      tickVal = "every 1 week";
      tickFormat = "%m-%d";
      break;
    case "months":
      tickVal = "every 1 month";
      tickFormat = "%Y-%m";
      break;
    default:
      throw new Error("Invalid interval type");
  }

  return (
    <div style={{ height: "400px", width: "45%" }}>
      <h2 style={{ textAlign: "center" }}>{title}</h2>
      <ResponsiveScatterPlot
        data={data}
        margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
        xScale={{
          type: "time",
          format: "%Y-%m-%d %H:%M:%S",
          precision: "hour",
        }}
        xFormat="time:%H:%M"
        yScale={{ type: "linear", min: "auto", max: "auto" }}
        yFormat=">-.2f"
        colors={["#0067C6"]}
        tooltip={tooltipContent}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          format: tickFormat,
          tickValues: tickVal,
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: "Time",
          legendPosition: "middle",
          legendOffset: 36,
        }}
        axisLeft={{
          tickSize: 5,
          tickPadding: 5,
          tickRotation: 0,
          legend: yAxisTitle,
          legendOffset: -40,
          legendPosition: "middle",
        }}
        blendMode="multiply"
        // pointSize={10}
        // pointColor={{ theme: 'background' }}
        // pointBorderWidth={2}
        // pointBorderColor={{ from: 'serieColor' }}
        // pointLabelYOffset={-12}
        useMesh={true}
      />
    </div>
  );
};

/**
 * A function to crop the data to the last 12 hours/days/weeks/months
 * @param data: chatBotResponseDataset
 * @param intervalType
 * @returns chatBotResponseDataset
 */
function cropData(
  data: ChatBotResponseDataset,
  intervalType: IntervalType
): ChatBotResponseDataset {
  const now = new Date();
  let cutoff = new Date();
  switch (intervalType) {
    case "hours":
      cutoff.setHours(now.getHours() - 12);
      break;
    case "days":
      cutoff.setDate(now.getDate() - 7);
      break;
    case "weeks":
      cutoff.setDate(now.getDate() - 28);
      break;
    case "months":
      cutoff.setMonth(now.getMonth() - 12);
      break;
    default:
      cutoff.setHours(now.getHours() - 12);
  }
  return data.map((series) => {
    return {
      ...series,
      data: series.data.filter((point) => point.x > cutoff),
    };
  }) as ChatBotResponseDataset;
}

export default ChatBotResponseGraph;
