import { useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import { mockAuth } from "../utils/mockAuth";
import "../styles/Login.css";

const Login = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("");
  const navigate = useNavigate();

  const handleLogin = async (e: any) => {
    setStatus("");
    e.preventDefault();
    try {
      setStatus("Logging in...");
      // API authentication request here
      var success = username === "admin" && password === "admin";
      await mockAuth(success, 1000);
      // update auth status
      navigate && navigate("/summary");
    } catch (e: any) {
      setStatus(e.message);
    }
  };

  return (
    <>
      <div className="banner">
        <h6 className="coast-companion">
          coast<span style={{ fontWeight: "200" }}>companion</span>
        </h6>
      </div>
      <form className="login-form" onSubmit={handleLogin}>
        <h6 className="header">Log In to Manage Coast Companion</h6>
        <div className="login-box">
          {status ? (
            status === "Logging in..." ? (
              <p className="login-message">{status}</p>
            ) : (
              <p className="error-message">{status}</p>
            )
          ) : (
            <p className="no-message" />
          )}
          <div className="login-fields">
            <p className="input-desc username-desc">Username</p>
            <input
              className="username"
              type="text"
              name="username"
              value={username}
              placeholder="your username"
              onChange={(e) => setUsername(e.target.value)}
            />
            <p className="input-desc">Password</p>
            <input
              className="password"
              type="password"
              name="password"
              value={password}
              placeholder="your password"
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <button className="submit-button" type="submit">
            Continue &rarr;
          </button>
          <Link to="/login" className="forgot-password">
            Forgot password?
          </Link>
        </div>
      </form>
    </>
  );
};

export default Login;
