const MaintainKB = () => {
  const [modal, setModal] = useState(false);
  const [loading, setLoading] = useState(true);
  const [nodes, setNodes] = useState<any[]>([]);
  const [search, setSearch] = useState("");

  const handleSearch = (event: any) => {
    setSearch(event.target.value);
  };

  const toggleModal = () => {
    setModal(!modal);
  };

  if (modal) {
    document.body.classList.add("active-modal");
  } else {
    document.body.classList.remove("active-modal");
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        await mockRequest(true, 2000);
        setNodes(tempNodes);
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, []);

  const filterNodes = (nodes: any) => {
    return {
      nodes: nodes.filter((item: any) =>
        item.source.toLowerCase().includes(search.toLowerCase())
      ),
    };
  };

  const handleAddDataToKB = async () => {
    toggleModal();
    setLoading(true);

    try {
      await mockRequest(true, 2000);
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };

  async function handleUpdateClick(id: any, property: any) {
    // stub
    return await Promise.resolve();
  }

  async function handleArchiveClick(id: any, property: any) {
    // stub
    return await Promise.resolve();
  }

  const override: CSSProperties = {
    display: "block",
    margin: "0 auto",
  };

  const theme = useTheme([
    getTheme(),
    {
      Table: `
        --data-table-library_grid-template-columns:  50px auto auto 150px 150px 150px 150px;
        margin-bottom: 50px;
        box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px;
      `,
    },
  ]);

  const COLUMNS = [
    { label: "#", renderCell: (item: any) => item.id },
    { label: "Source", renderCell: (item: any) => item.source },
    {
      label: "Database Location",
      renderCell: (item: any) => item.databaseLocation,
    },
    { label: "File Type", renderCell: (item: any) => item.fileType },
    {
      label: "Update",
      renderCell: (item: any) => (
        <button
          className={"maintain-kb-button"}
          onClick={() => handleUpdateClick(item.id, "update")}
        >
          Update
        </button>
      ),
    },
    {
      label: "Archive",
      renderCell: (item: any) => (
        <button
          className={"maintain-kb-button"}
          onClick={() => handleArchiveClick(item.id, "archive")}
        >
          Archive
        </button>
      ),
    },
    {
      label: "Date",
      renderCell: (item: any) =>
        item.date.toLocaleDateString("en-US", {
          month: "short",
          day: "numeric",
          year: "numeric",
        }),
    },
  ];

  return (
    <>
      <Nav />
      {modal && (
        <form className="modal" onSubmit={handleAddDataToKB}>
          <div onClick={toggleModal} className="overlay"></div>
          <div className="modal-content">
            <h2>Add Data</h2>
            <div className="modal-user-input-content">
              <p>File Name:</p>
              <input
                className="file-name-input"
                id="file-name-input"
                type="text"
                required
              />
              <p>File Upload:</p>
              <input className="file-upload" type="file" required />
              <button className="add-data-to-kb-button" type="submit">
                Add Data to Knowledge Base
              </button>
              <button
                className="close-modal"
                type="button"
                onClick={toggleModal}
              >
                Close
              </button>
            </div>
          </div>
        </form>
      )}
      {!modal && (
        <div className="maintain-kb-page">
          <div className="clip-loader">
            <ClipLoader
              color={"#0050F8"}
              loading={loading}
              cssOverride={override}
              size={80}
              aria-label="Loading Spinner"
              data-testid="loader"
            />
          </div>
          {!loading && (
            <div className="kb-container">
              <div className="kb-header">
                <div>
                  <h2 className="knowledge-base-title">Knowledge Base</h2>
                  <p className="last-updated">
                    Last Updated: 02/17/2024 12:01 PST
                  </p>
                  <label className="search-by-source" htmlFor="search">
                    Search by Source:&nbsp;
                    <input
                      className="kb-search"
                      id="search"
                      type="text"
                      value={search}
                      onChange={handleSearch}
                    />
                  </label>
                </div>
                <div>
                  <button className="add-data-button" onClick={toggleModal}>
                    + Add Data
                  </button>
                </div>
              </div>
              <CompactTable
                columns={COLUMNS}
                data={filterNodes(nodes)}
                theme={theme}
                layout={{ custom: true }}
              />
            </div>
          )}
        </div>
      )}
    </>
  );
};

export default MaintainKB;
