import React, {  CSSProperties, useState, useEffect,  } from "react";
import Nav from "../components/Nav";
import "../styles/Summary.css";
import ChatBotResponseGraph from "../components/ChatBotResponseGraph";
import UserSatisfactionGraph from "../components/UserSatisfactionGraph";
import { ClipLoader } from "react-spinners";
import { SelectField } from "@aws-amplify/ui-react";
import { mockRequest } from "../utils/mockRequest";
import { ChatBotResponseDataset, SatisfactionDataset, IntervalType, } from "../types/admin";

const Summary = () => {
  const [intervalType, setIntervalType] = useState<IntervalType>("hours");
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState({});

  const override: CSSProperties = {
    display: "block",
    margin: "0 auto",
    float: "inline-start",

  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        await mockRequest(true, 2000);
        setData({ data: data });
        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchData();
  }, []);

  const handleSelectChange = (event: {
    target: { value: React.SetStateAction<string> };
  }) => {
    let setVal = event.target.value as IntervalType;
    setIntervalType(setVal);
  };

  return (
    <>
      <Nav />
      <div className="summary-page">

      {loading && 
        <div className="clip-loader" style={{ height: '100vh', width: '100vw' }}>
            <ClipLoader
              color={"#0050F8"}
              loading={loading}
              cssOverride={override}
              size={80}
              aria-label="Loading Spinner"
              data-testid="loader"
            />
          </div>}
        {!loading && (
          <div className="user-ratings-container" id="charts">
            <h2 className="user-ratings-title">User Ratings</h2>
            <p className="last-updated">Last Updated: 02/17/2024 12:01 PST</p>
            <div className="user-select-container">
            <SelectField
                label="Select Time Interval"
                value={intervalType}
                onChange={handleSelectChange}
                id="chart2b">
                <option value="hours">Last 12 hours</option>
                <option value="days">Last 12 days</option>
                <option value="weeks">Last 12 weeks</option>
                <option value="months">Last 12 months</option>
              </SelectField>
            </div>


            
            <div className="user-ratings-graph-container">
              <ChatBotResponseGraph data={dataResp} intervalType={intervalType}  />
              <p></p> <p></p>
              <UserSatisfactionGraph
                data={dataSatisfaction}
                intervalType={intervalType}
              />
            </div>

          </div>
        )}

      </div>
    </>
  );
};

